<h1 id="common-code-mistakes" tabindex="-1"><a class="header-anchor" href="#common-code-mistakes" aria-hidden="true">#</a> Common Code Mistakes</h1>
<p>In this lecture we take a look at common mistakes students do in the code they write. But first some introduction to why even small mistakes are very bad.</p>
<h2 id="why-are-small-mistakes-such-a-big-deal" tabindex="-1"><a class="header-anchor" href="#why-are-small-mistakes-such-a-big-deal" aria-hidden="true">#</a> Why are small mistakes such a big deal?</h2>
<p>Many students think that many of the common mistakes mentioned in this lecture are something they don't need to worry about. <em>My code compiles, it works as intended, so what's the problem?</em> Well, the problem is that these students have only thought about delivering a <em>functional</em> solution, not a <em>good</em> solution. These students often think that they are the only ones who ever will read the code they write, or maybe even that no one at all will ever read the code they write, but that is usually not the case:</p>
<ul>
<li>When the application has been fully implemented, a <em>quality assurance expert</em> might read all the code so she can guarantee customers you deliver what has been agreed on. Then it's very important the code is easy to read.</li>
<li>When the application has been fully implemented, a <em>security expert</em> might read all the code so she can guarantee customers the application doesn't contain any known security vulnerabilities. Then it's very important the code is easy to read.</li>
<li>After the application has been fully implemented, <em>customers might discover bugs</em> that needs to be fixed. A programmer other than you might be assigned the task to fix the bug (you might for example have started to work on other projects and are busy with them), and then it's very important the code is easy to read for the new programmer.</li>
<li>After the application has been fully implemented, <em>customers might want additional features</em> added to the application. A programmer other than you might be assigned the task to add it (you might for example have started to work on other projects and are busy with them), and then it's very important the code is easy to read for the new programmer.</li>
<li>Etc.</li>
</ul>
<p>When your code is executed by a computer, it is very important that the code is fully functional and, if not optimized, at least not terribly slow. But it is even more important that others easily can read your code, because humans are much more expensive to use than computers. For example, if you have written good code that is easy to read, a security expert might need one day to look through all the code in the application to verify that it doesn't contain any known security vulnerabilities. But if the code instead is hard to read, she might need a week to do that. And if she is having difficulties to read the code, so will the quality assurance guy, who also needs a week instead of a single day to look through the code. And when adding a new feature in the future, which would take 4 hours to do with good code, will take 3 days with the bad code, and most likely new bugs are introduced with this new feature because the programmer who added it didn't understand how all the existing code worked, so she made assumptions of how parts of the code worked instead of knowing, and some of these assumptions will be wrong and need to be corrected later.</p>
<p>So, a programmer that is lazy and considers himself to be finish when the code is functional is not a good programmer. <strong>When the code is functional, you are half way there</strong>: you have implemented the feature in such way that computers can execute your code. Half of your work remains: to improve the quality of the code by re-structuring it and making it more readable so other humans easily can understand how your code works.</p>
<h2 id="but-why-are-small-mistakes-such-a-big-deal" tabindex="-1"><a class="header-anchor" href="#but-why-are-small-mistakes-such-a-big-deal" aria-hidden="true">#</a> But why are <em>small</em> mistakes such a big deal?</h2>
<p><em>OK, I misspelled the name of a variable once, and at a few places I didn't stick to my indentation convention. Other humans can still read and understand my code although these small mistakes exist, what's the big deal?</em></p>
<p>Programmers are only humans, so one or two of these mistakes in 5 000 lines of code are understandable. But if more than that exists, it's simply a job poorly done by the programmer. Programming is quite abstract and can be hard to understand, so let's compare it to other professions:</p>
<ul>
<li>Do you think a painter do a good job if he paints 99.9% of the wall in one color, but there's a small line in the middle of the wall in another color?<br>
<em>What's the big deal, it's only a small line</em>?</li>
<li>Do you think a car manufacturer do a good job if the steering wheel only works 99.9% of the cases?<br>
<em>What's the big deal, you can still steer the car most of the time</em>?</li>
<li>Do you think an author do a good job if all sentences in her book always start with a lower case character?<br>
<em>What's the big deal, most characters are in the correct case</em>?</li>
</ul>
<p>Hopefully you agree in these other professions, even small mistakes, such as those listed above, are quite bad, so for students studying to these professions it's very important to learn that these small mistakes are not acceptable. The same is true for students studying programming, the only difference is that it's harder for beginners to realize that small programming mistakes are just as bad as the mistakes listed above.</p>
<p>Of course there are some nice xkcd:s about code quality:</p>
<ul>
<li><a href="https://xkcd.com/1513/" target="_blank" rel="noopener noreferrer">https://xkcd.com/1513/<OutboundLink/></a></li>
<li><a href="https://xkcd.com/1695/" target="_blank" rel="noopener noreferrer">https://xkcd.com/1695/<OutboundLink/></a></li>
<li><a href="https://xkcd.com/1833/" target="_blank" rel="noopener noreferrer">https://xkcd.com/1833/<OutboundLink/></a></li>
</ul>
<h2 id="common-student-mistakes" tabindex="-1"><a class="header-anchor" href="#common-student-mistakes" aria-hidden="true">#</a> Common student mistakes</h2>
<p>So, what are some of the common mistakes students do in their code?</p>
<h3 id="indent-wrong" tabindex="-1"><a class="header-anchor" href="#indent-wrong" aria-hidden="true">#</a> Indent wrong</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">getSum</span><span class="token punctuation">(</span><span class="token parameter">x<span class="token punctuation">,</span> y</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">const</span> sum <span class="token operator">=</span> x <span class="token operator">+</span> y
        <span class="token keyword">return</span> sum
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div></template>
<template v-slot:example-1-good>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">getSum</span><span class="token punctuation">(</span><span class="token parameter">x<span class="token punctuation">,</span> y</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">const</span> sum <span class="token operator">=</span> x <span class="token operator">+</span> y
    <span class="token keyword">return</span> sum
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div></template>
<template v-slot:example-2-bad>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">logSum</span><span class="token punctuation">(</span><span class="token parameter">numbers</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">let</span> sum <span class="token operator">=</span> <span class="token number">0</span>
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> number <span class="token keyword">of</span> numbers<span class="token punctuation">)</span>
        sum <span class="token operator">+=</span> number
        console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>sum<span class="token punctuation">)</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div></template>
<template v-slot:example-2-good>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">logSum</span><span class="token punctuation">(</span><span class="token parameter">numbers</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">let</span> sum <span class="token operator">=</span> <span class="token number">0</span>
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> number <span class="token keyword">of</span> numbers<span class="token punctuation">)</span>
        sum <span class="token operator">+=</span> number
    console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>sum<span class="token punctuation">)</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div></template>
<template v-slot:mistake>
<p>The code has been indented wrong.</p>
</template>
<template v-slot:problem>
<p>Indentation is a tool we have to make the code more readable. Used wrong, it instead makes the code less readable, and it would be better to not indent the code at all. That way, at least the reader would not mistake an incorrect indentation for a correct indentation.</p>
<p>Compare to text in natural languages (e.g. English and Swedish): when you come across a word starting with an uppercase character you can assume that word is a name (<em>Alice</em>, <em>England</em>, etc.) (unless it's the first word in a sentence). Using this wrong makes the reader misinterpret the meaning of the text, for example:</p>
<ul>
<li><em>alice is young</em> means that the reader won't interpret <em>alice</em> as a name, and the text has another meaning than the writer intended.</li>
<li><em>the Dog is old</em> means the reader will interpret <em>Dog</em> as a name, and the text has another meaning than the writer intended.</li>
</ul>
<p>Even if you only have 2-3 of these mistakes, the reader can't trust that you use lowercase/uppercase characters correct anywhere in your text, and she must read all your text very carefully, and that requires bigger concentration and takes longer time.</p>
</template>
<template v-slot:solution>
<p>Always properly indent the code.</p>
</template>
</ReportMistake>
<h3 id="multiple-indentation-conventions" tabindex="-1"><a class="header-anchor" href="#multiple-indentation-conventions" aria-hidden="true">#</a> Multiple indentation conventions</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">logSum</span><span class="token punctuation">(</span><span class="token parameter">numbers</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  <span class="token keyword">let</span> sum <span class="token operator">=</span> <span class="token number">0</span>
  <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> number <span class="token keyword">of</span> numbers<span class="token punctuation">)</span><span class="token punctuation">{</span>
          sum <span class="token operator">+=</span> number
  <span class="token punctuation">}</span>
  console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>sum<span class="token punctuation">)</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div></template>
<template v-slot:example-1-good>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">logSum</span><span class="token punctuation">(</span><span class="token parameter">numbers</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">let</span> sum <span class="token operator">=</span> <span class="token number">0</span>
    <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> number <span class="token keyword">of</span> numbers<span class="token punctuation">)</span><span class="token punctuation">{</span>
        sum <span class="token operator">+=</span> number
    <span class="token punctuation">}</span>
    console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>sum<span class="token punctuation">)</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div></template>
<template v-slot:example-2-bad>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">logSum</span><span class="token punctuation">(</span><span class="token parameter">numbers</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token keyword">if</span><span class="token punctuation">(</span><span class="token number">0</span> <span class="token operator">&lt;</span> numbers<span class="token punctuation">.</span>length<span class="token punctuation">)</span><span class="token punctuation">{</span>
             <span class="token keyword">let</span> sum <span class="token operator">=</span> <span class="token number">0</span>
             <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> number <span class="token keyword">of</span> numbers<span class="token punctuation">)</span><span class="token punctuation">{</span>
                     sum <span class="token operator">+=</span> number
             <span class="token punctuation">}</span>
             console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>sum<span class="token punctuation">)</span>
        <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
         console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"No sum to log."</span><span class="token punctuation">)</span>
        <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div></template>
<template v-slot:example-2-good>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">function</span> <span class="token function">logSum</span><span class="token punctuation">(</span><span class="token parameter">numbers</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">if</span><span class="token punctuation">(</span><span class="token number">0</span> <span class="token operator">&lt;</span> numbers<span class="token punctuation">.</span>length<span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token keyword">let</span> sum <span class="token operator">=</span> <span class="token number">0</span>
        <span class="token keyword">for</span><span class="token punctuation">(</span><span class="token keyword">const</span> number <span class="token keyword">of</span> numbers<span class="token punctuation">)</span><span class="token punctuation">{</span>
            sum <span class="token operator">+=</span> number
        <span class="token punctuation">}</span>
        console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>sum<span class="token punctuation">)</span>
    <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
        console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"No sum to log."</span><span class="token punctuation">)</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div></template>
<template v-slot:mistake>
<p>The code uses multiple indentation conventions (&quot;indentation of different sizes&quot;).</p>
</template>
<template v-slot:problem>
<p>It's hard to see how nested the code is when each indentation level is of different size. As always, it's <strong>better to be consistent</strong>.</p>
</template>
<template v-slot:solution>
<p>Always use the same indentation convention (e.g. <code>2 spaces</code>, <code>4 spaces</code>, <code>8 spaces</code> or <code>1 tab</code>, etc.) for all your code. Files of different types can use different indentation convention if that is suitable.</p>
<div class="custom-container tip"><p class="custom-container-title">Show white spaces!</p>
<p>Most code editors do by default show space and tab characters as transparent boxes. This makes it hard to see if one is mixing spaces and tabs when one indents the code. But any proper code editor has a setting you can change to display spaces as <code>·</code> and tabs as <code>→</code>, or similar. We strongly recommend you to enable this, so you can be sure that you don't mix indentation conventions.</p>
</div>
</template>
</ReportMistake>
<h3 id="not-using-mnemonic-names" tabindex="-1"><a class="header-anchor" href="#not-using-mnemonic-names" aria-hidden="true">#</a> Not using mnemonic names</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">if</span><span class="token punctuation">(</span>username <span class="token operator">==</span> uname<span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token comment">// Correct username entered!</span>
<span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
    console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"The wrong username '"</span><span class="token operator">+</span>username<span class="token operator">+</span><span class="token string">"' was entered."</span><span class="token punctuation">)</span>
     <span class="token comment">// By only looking at the line above, we can't be sure we used the correct variable.</span>
     <span class="token comment">// Need to look at the definition of the variables to be certain... 😒</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div></template>
<template v-slot:example-1-good>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">if</span><span class="token punctuation">(</span>enteredUsername <span class="token operator">==</span> correctUsername<span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token comment">// Correct username entered!</span>
<span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
    console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"The wrong username '"</span><span class="token operator">+</span>enteredUsername<span class="token operator">+</span><span class="token string">"' was entered."</span><span class="token punctuation">)</span>
    <span class="token comment">// We can be sure that we used the correct variable on the line above.</span>
    <span class="token comment">// No need for us to look at the definition of the variables 😀</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div></template>
<template v-slot:example-2-bad>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> movies <span class="token operator">=</span> <span class="token number">4</span> <span class="token comment">// "movies" sounds like a collection of multiple movie objects, like:</span>
<span class="token comment">// movies = [{title: "Die Another Day", title: "Titanic"}]</span>

<span class="token comment">// Much further down...:</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"I have "</span><span class="token operator">+</span>movies<span class="token operator">+</span><span class="token string">" movies."</span><span class="token punctuation">)</span>
<span class="token comment">// The line above seems wrong, should rather use movies.length?</span>
<span class="token comment">// Need to go to the definition of the "movies" variable to check... 😒</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div></template>
<template v-slot:example-2-good>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> numberOfMovies <span class="token operator">=</span> <span class="token number">4</span>

<span class="token comment">// Much further down...:</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"I have "</span><span class="token operator">+</span>numberOfMovies<span class="token operator">+</span><span class="token string">" movies."</span><span class="token punctuation">)</span>
<span class="token comment">// The line above is obviously correct 😀</span>
<span class="token comment">// No need to go to the definition of the "movies" variable to check.</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><p>OR</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> movies <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">{</span>title<span class="token operator">:</span> <span class="token string">"Die Another Day"</span><span class="token punctuation">,</span> title<span class="token operator">:</span> <span class="token string">"Titanic"</span><span class="token punctuation">}</span><span class="token punctuation">]</span>

<span class="token comment">// Much further down...:</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"I have "</span><span class="token operator">+</span>movies<span class="token punctuation">.</span>length<span class="token operator">+</span><span class="token string">" movies."</span><span class="token punctuation">)</span>
<span class="token comment">// The line above makes total sense, is probably correct 😀</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div></template>
<template v-slot:mistake>
<p>The names on variables, functions, classes, files, etc. are not descriptive (the names don't describe what they represent).</p>
</template>
<template v-slot:problem>
<p>The code becomes very hard to read, because the reader can't understand what a specific line of code using these names do, but have to go and look at the definition of these names elsewhere to understand what they represent, and then go back to the initial line of code to understand how that one works. Too much work...</p>
</template>
<template v-slot:solution>
<p>Always use good descriptive names on everything. Optimally one should be able to understand what each line of code do/how each line of code works only by reading that line of code.</p>
</template>
</ReportMistake>
<h3 id="using-hardcoded-values" tabindex="-1"><a class="header-anchor" href="#using-hardcoded-values" aria-hidden="true">#</a> Using hardcoded values</h3>
<ReportMistake>
<template v-slot:example-1-bad>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// The condition below contains no info about why username is compared to "Alice".</span>
<span class="token keyword">if</span><span class="token punctuation">(</span>username <span class="token operator">==</span> <span class="token string">"Alice"</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token comment">// ...</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div></template>
<template v-slot:example-1-good>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> <span class="token constant">ADMIN_USERNAME</span> <span class="token operator">=</span> <span class="token string">"Alice"</span>

<span class="token comment">// The condition below obviously checks if the user is an admin.</span>
<span class="token keyword">if</span><span class="token punctuation">(</span>username <span class="token operator">==</span> <span class="token constant">ADMIN_USERNAME</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token comment">// ...</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div></template>
<template v-slot:example-2-bad>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>


<span class="token keyword">const</span> errorCodes <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span>

<span class="token comment">// The condition below contains no info about why we have this condition.</span>
<span class="token keyword">if</span><span class="token punctuation">(</span>name<span class="token punctuation">.</span>length <span class="token operator">&lt;</span> <span class="token number">3</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    errorCodes<span class="token punctuation">.</span><span class="token function">push</span><span class="token punctuation">(</span><span class="token number">1</span><span class="token punctuation">)</span> <span class="token comment">// No info about what 1 means. Is this really correct?</span>
<span class="token punctuation">}</span>

<span class="token comment">// ...</span>

<span class="token keyword">function</span> <span class="token function">errorCodeToMessage</span><span class="token punctuation">(</span><span class="token parameter">errorCode</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">switch</span><span class="token punctuation">(</span>errorCode<span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token keyword">case</span> <span class="token number">1</span><span class="token operator">:</span> <span class="token comment">// No easy way to check that this translation is correct.</span>
            <span class="token keyword">return</span> <span class="token string">"Name needs to contain at least 3 characters."</span>
        <span class="token comment">// ...</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br></div></div></template>
<template v-slot:example-2-good>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> <span class="token constant">MIN_NAME_LENGTH</span> <span class="token operator">=</span> <span class="token number">3</span>
<span class="token keyword">const</span> <span class="token constant">ERROR_CODE_NAME_TOO_SHORT</span> <span class="token operator">=</span> <span class="token number">1</span>

<span class="token keyword">const</span> errorCodes <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span>

<span class="token comment">// The condition below obviously checks if the name is too short.</span>
<span class="token keyword">if</span><span class="token punctuation">(</span>name<span class="token punctuation">.</span>length <span class="token operator">&lt;</span> <span class="token constant">MIN_NAME_LENGTH</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    errorCodes<span class="token punctuation">.</span><span class="token function">push</span><span class="token punctuation">(</span><span class="token constant">ERROR_CODE_NAME_TOO_SHORT</span><span class="token punctuation">)</span> <span class="token comment">// Using this error code here is obviously correct.</span>
<span class="token punctuation">}</span>

<span class="token comment">// ...</span>

<span class="token keyword">function</span> <span class="token function">errorCodeToMessage</span><span class="token punctuation">(</span><span class="token parameter">errorCode</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">switch</span><span class="token punctuation">(</span>errorCode<span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token keyword">case</span> <span class="token constant">ERROR_CODE_NAME_TOO_SHORT</span><span class="token operator">:</span> <span class="token comment">// The translation here is obviously correct.</span>
            <span class="token keyword">return</span> <span class="token string">"Name needs to contain at least "</span><span class="token operator">+</span><span class="token constant">MIN_NAME_LENGTH</span><span class="token operator">+</span><span class="token string">" characters."</span>
        <span class="token comment">// ...</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br></div></div></template>
<template v-slot:mistake>
<p>Harcoded values are used in the middle of the code.</p>
</template>
<template v-slot:problem>
<p>A hardcoded value contains no information about what that value represent; when someone reads a hardcoded value, they must guess what the value represents. This makes the code hard to understand. For example, when comparing <code>username</code> to <code>&quot;Alice&quot;</code>, is that because <code>&quot;Alice&quot;</code> is admin or because <code>&quot;Alice&quot;</code> is a banned member? The reader won't know until she has read the other lines of code close to this one (should not be needed!).</p>
</template>
<template v-slot:solution>
<p>Don't use hardcoded values in the middle of the code. Put the values in descriptive constants instead. This way, the code becomes more readable, and it's also easy to change these values in the future (if a hardcoded value is used at multiple values, you just need to change it where you create the constant, and not at each place you use the hardcoded value).</p>
</template>
</ReportMistake>